/*Theme Dropdown*/
body.light .dropdown-content li > a,
body.light .dropdown-content li > span {
  color: #000;
}

body.dark .dropdown-content li > a,
body.dark .dropdown-content li > span {
  color: #fff;
  background-color: #3a3a3a;
}

body.dark .dropdown-content li > a:hover,
body.dark .dropdown-content li > span:hover {
  background-color: var(--theme-dark);
}

body.light svg.mn-logo path,
body.light svg.oci-logo path {
  fill: var(--theme-dark);
}
body.dark svg.mn-logo path,
body.dark svg.oci-logo path {
  fill: var(--theme-light);
}

svg.oci-logo path.st1 {
  fill: #77c7d3 !important;
}
svg.oci-logo path.st2 {
  fill: #f7b376 !important;
}

/*Theme Explicit*/
.bg-light {
  background-color: var(--theme-light) !important;
  color: var(--theme-dark) !important;
}
.bg-dark {
  background-color: var(--theme-dark) !important;
  color: var(--theme-light) !important;
}

/* Media */

@media only screen and (min-width: 601px) {
  .container {
    width: 85%;
  }
}

@media only screen and (max-width: 600px) {
  footer.container.mn-footer-container {
    padding-right: 0;
    padding-left: 0;
  }

  .mn-feature-container .selected-features,
  .mn-container .sticky {
    position: relative !important;
    top: 0;
    overflow-y: scroll;
  }
}

@media only screen and (max-width: 400px) {
  .row .col.xs6 {
    width: 50%;
    margin-left: auto;
    left: auto;
    right: auto;
    padding-bottom: 5px;
  }
}

.button-row .action-button-icon {
    display: block;
}
/* Media */
@media only screen and (max-width: 788px) {
    .button-row .action-button-icon {
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    .button-row .btn {
        font-size: 0.9em;
        padding: 0 10px;
    }
}

@media only screen and (max-width: 500px) {
    .button-row .btn {
        font-size: 0.7em;
        padding: 0 4px;
    }
}

@media only screen and (max-width: 400px) {
    .button-row .btn {
        font-size: 0.7em;
        padding: 0 10px;
    }
}

@media only screen and (max-width: 225px) {
    .button-row .btn {
        padding: 0 2.5em;
    }

    .button-row .btn {
        font-size: 0;
        margin: auto;
    }

    .button-row .btn i {
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        font-size: 16px;
    }

    .button-row .action-button-icon {
        display: block;
    }
}

.d-flex {
  display: flex;
}

.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}
.flex {
  flex: 1 1;
}

.flex-grow {
  flex-grow: 1;
}
.flex-shrink {
  flex-shrink: 1;
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  z-index: 2001;
}

.MuiPopover-root {
  z-index: 2002 !important;
}

.clickable {
  cursor: pointer;
}

.jcc {
  justify-content: center;
}

.aic {
  align-items: center;
}
.nowrap {
  white-space: nowrap;
}
.o-scroll {
  overflow: scroll;
}

.modal {
  width: 35%;
  min-width: 320px;
  z-index: 9998 !important;
}

.modal.preview h4,
.modal.diff h4 {
  padding-bottom: 10px;
  margin-bottom: 0;
  position: fixed;
}

.modal h4 {
  font-size: 1.5rem;
}

.modal h5 {
  font-size: 1.3rem;
}

.modal.modal-lg {
  width: 65%;
}

.modal.modal-xl {
  width: 90%;
}

.modal.mn-feature-modal,
.modal.diff,
.modal.preview {
  width: 75%;
  max-height: 90%;
  height: 90%;
}

.modal.dark,
.modal.dark .modal-footer {
  background-color: var(--theme-dark);
  border-top: 1px solid rgb(241 240 240 / 30%);
}

.modal.light,
.modal.light .modal-footer {
  background-color: var(--theme-light);
  border-top: 1px solid rgb(103 103 103 / 10%);
}

.dark .modal-overlay {
  background: rgb(38 38 38);
  opacity: 0.8 !important;
}

/* Modal Media */
@media only screen and (max-width: 800px) {
  .modal {
    width: 60%;
  }
}

@media only screen and (max-width: 600px) {
  .modal {
    height: 100% !important;
    max-height: 100% !important;
    width: 100% !important;
    max-width: 100% !important;
    top: 0% !important;
  }
}

/* We need a bit of a heavy hand on a number of elements that
 * are pre-built to Materialize Standards.
 */

.button-row button {
  text-transform: uppercase;
}

/* Label above the Text input */
.input-field > label:not(.label-icon).active {
  -webkit-transform: translateY(-14px) scale(1);
          transform: translateY(-14px) scale(1);
}

/* Label Select Options */
.MuiInputLabel-shrink {
  -webkit-transform: translate(0, 1.5px) scale(1) !important;
          transform: translate(0, 1.5px) scale(1) !important;
}

.button-row button,
.input-field,
.mn-input,
.MuiSelect-root.MuiInput-input,
.MuiListItem-button.Mui-selected,
.radio-group,
.radio-text {
  font-family: 'Fira Sans', sans-serif !important;
  font-size: 1rem !important;
  font-weight: 500 !important;
}

.radio-group label,
.input-field label {
  font-family: 'Fira Sans', sans-serif !important;
  font-size: 0.75rem !important;
  font-weight: 400 !important;
}

.MuiPopover-root {
  z-index: 2002 !important;
}

.MuiSnackbar-root.error-view {
  z-index: 9999;
}

.modal-overlay {
  z-index: 2004 !important;
}

.modal {
  z-index: 9998 !important;
}

.sticky {
  z-index: 2001;
}

.mn-footer-container {
  z-index: 1001;
}

